.friends {
  background: #fff;
  padding-top: 0.333333rem;
}

.friends .f-person {
  width: 9.466667rem;
  height: 3.213333rem;
  background: linear-gradient(0deg, #e62129 0%, #ff5501 100%);
  border-radius: 0.133333rem;
  margin: 0 auto;
  padding: 0.746667rem 0 0 0.36rem;
  box-sizing: border-box;
  position: relative;
}

.friends .f-person .returnClass {
  position: absolute;
  right: 0.36rem;
  top: 0.746667rem;
  color: #fff;
  font-size: 0.373333rem;
}

.friends .f-person .p-img {
  height: 1.72rem;
  width: 1.72rem;
  border-radius: 50%;
  background: #fff;
  overflow: hidden;
  margin-right: 0.2rem;
  float: left;
}

.friends .f-person .p-img img {
  width: 100%;
}

.friends .f-person .p-name {
  width: 6.666667rem;
  float: left;
}

.friends .f-person .p-name .name {
  color: #fff;
  font-size: 0.48rem;
}

.friends .f-person .p-name .company {
  color: #fff;
  font-size: 0.373333rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.friends .f-person .p-name .start {
  color: #DDD;
  font-size: 0.266667rem;
  margin-top: 0.053333rem;
}

.friends .f-person .p-name .s-top {
  margin-top: 0.133333rem;
}

.friends .f-person .p-name .p-ye {
  color: #FFEE31;
}


